export const RouteWithSubRoutes=(route)=>{
	if(route.pri){
		console.log('1')
		console.log(route.routes)
		return(
			<PrivateRoute {...route} routes={route.routes} />
		)
	}
	else{
		return (
			<Route
			path={route.path}
			render={props=>(
				<route.component {...props} route={route.routes} />
			)}
			/>
		)
	}
}

function PrivateRoute({ component:Com, ...rest }) {
	return (
	  <Route
		{...rest}
		render={({ location }) =>
		   localStorage.getItem('userinfo')
		   ?<Com {...rest} /> 
		   : (
			<Redirect
			  to={{
				pathname: "/login",
				state: location 
			  }}
			/>
		  )
		}
	  />
	);
  }
