import React from 'react'
import Home from './Home';
import Doc from './Doc';
import Tuto from './Tuto';
import Blog from './Blog';
import Comm from './Comm';
import Login from './Login';
import Api from './Api';
import  Hook from './Hook';
import Guide from './Guide';
import Core from './Core';

const Routes=[
    {
        path:'/',
        exact:true,
        component:Home
    },
    {
        path:'/Doc',
        component:Doc,
        pri:true,
        routes:[{
            path:'/doc/core',
            component:Core,
        },
        {
            path:'/doc/guide',
            component:Guide,
        },
        {
            path:'/doc/api',
            component:Api,
        },
        {
            path:'/doc/hook',
            component:Hook,
        }
    ]
    }
    ,
    {
        path:'/Tuto',
        component:Tuto
    }
    ,{
        path:'/Comm',
        component:Comm
    }
    ,{
        path:'/Blog',
        component:Blog
    }
    ,{
        path:'/Login',
        component:Login
    }
]
export default Routes;